* {
  margin: 0;
}

header {
  width: 100%;
  height: 50px;
  position: fixed;
  background-color: white;
  border-bottom: 2px solid black;
  top: 0;
  z-index: 999;
}

.header__logo {
  height: 24px;
  margin-left: 10px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.header__title {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: absolute;
  width: 65%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.header__line {
  width: 26px;
  height: 0px;
  border: 1px solid #000000;
  transform: rotate(90deg);
}

.header__link {
  font-family: AdiHaus;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 25px;
  text-transform: uppercase;
  text-decoration: none;
  color: #000000;
}

.icons__container {
  display: flex;
  justify-content: space-between;
  width: 70px;
  height: 28px;
  margin-right: 10px;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  position: absolute;
}

.lyocell {
  height: 100vh;
  background-image: url(../img/lyocell.png);
  background-size: cover;
}

footer {
  margin-top: 60px;
  height: 400px;
  width: 100%;
  background-color: black;
  display: flex;
  align-items: center;
}

.footer_left {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 0 0 2%;
  height: 90%;
  width: 48%;
  border-right: 2px solid #bcafaf;

  p {
    font-family: AdiHaus;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 37px;
    color: #ffffff;
  }
}

.footer_right {
  display: flex;
  justify-content: space-between;
  margin: 0 0 0 2%;
  height: 90%;
  width: 35%;

  .lieu {
    width: 270px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  p {
    font-family: AdiHaus;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 37px;

    color: #ffffff;
  }
}

.footer_right-text {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.footer_right-img {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
